.header {
  max-width: 880px;
  min-height: 74px;
  width: 100%;
  padding-top: 45px;
  border-bottom: 1px solid rgba(84, 84, 84, 0.7);
  box-sizing: content-box;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

@media screen and (max-width: 600px) {
  .header {
    min-height: 56px;
    padding-top: 28px;
  }
}